<template>
  <!-- Rounded switch -->
    <div class="switch-container">
      <h1 id="switch-dev"> WebDev </h1>
      <label class="switch">
        <input type="checkbox" v-model="componentDev">
        <span class="slider round"></span>
      </label>
      <h1 id="switch-sales"> Sales </h1>
    </div>
</template>

<script>
export default {
  name: 'SiteSwitch',
  data () {
    return {
      componentSales: false
    }
  }
}
</script>

<style>


.switch-container {
  position: relative;
  margin-top: 15em;
}

#switch-sales {
  position: relative;
  display: inline-block;
  width: 5em;
}

#switch-dev {
  display: inline-block;
  position: relative;
  width: 5em;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  top: .5em;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: orange;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

</style>



